export default {
	data() {
		return {
			userInfo: {}
		}
	},
	onShow() {
		let userInfo = uni.getStorageSync('userInfo');
		this.userInfo = userInfo;
	},
	methods: {
		goUserInfo() {
			uni.navigateTo({
				url: '../userInfo/userInfo'
			})
		},
		goChangePhone() {
			uni.navigateTo({
				url: '../phone/phone'
			})
		},
		goPayCenter() {
			uni.navigateTo({
				url: '../paySetting/paySetting'
			})
		},
		gochangePwd() {
			uni.navigateTo({
				url: '../pwd/password'
			})
		},
		goSafeCenter() {
			uni.showToast({
				icon: 'none',
				title: '暂未开放'
			});
		},
		goAbout() {
			uni.navigateTo({
				url: '../about/about'
			});
		},
		logout() {
			uni.showModal({
				title: '提示',
				content: '确定要注销账号吗',
				success: (res) => {
					if (res.confirm) {
						uni.removeStorageSync('token')
						uni.removeStorageSync('userInfo')
						uni.showToast({
							title: '注销成功',
							success: () => {
								setTimeout(function() {
									uni.navigateTo({
										url: '../login/login'
									})
								}, 500);
							}
						})
					}
				}
			});
		}
	}
}
